@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
    font-family: 'lato', sans-serif;
}

h2 {
    font-family: 'lato', sans-serif;
    font-size: 1.1rem;
}

a {
    text-decoration: none;
    color: #000000;
}

.navbar {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-left: 10%;
}

.name {
    display: flex;
    flex-direction: row;
    font-size: 1rem;
}
.name:hover {
    cursor: pointer;
}

.name .e1 {
    color: #568ea6;
}
.name .e2 {
    color: #f0b7a4;
}

.name .e3 {
    color: #f18c8e;
}

.menu {
    width: 250px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.footer {
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    margin-left: 10%;
}

.footer p {
    font-family: 'lato', sans-serif;
    font-weight: 100;
    text-align: end;
}

.social {
    width: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

body {
    width: 80%;
}

.description img {
    width: 200px;
    border-radius: 200px;
}

.description p {
    width: 50%;
    margin-left: 8%;
    font-size: 1rem;
    font-family: 'Dosis', sans-serif;
}

.description {
    width: 60%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 25%;
    margin-top: 10%;
    margin-bottom: 10%;
}
img {
    width: 60%;
}

.projectInfo-container {
    width: 80%;
    margin-left: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.projectInfo-container p, h4 {
    font-family: 'Dosis', sans-serif;
}

.summary img {
    width: 90%;
    padding: 20px 0px;
}

.summary {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.roles {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
}

.roles-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 20px;
}

.roles-icon p {
    text-align: center;
}

.methods {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.methods-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 20px;
}

.methods-icon p {
    text-align: center;
}

.evidence {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    margin-top: 20px;
}

.quote {
    display: flex;
    flex-direction: column;
    width: 30%;
}

.headings {
    margin-top: 60px;
}

.insights {
    display: flex;
    flex-direction: column;
}

.insights-icon {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.insights-icon p {
    width: 60%;
    padding: 5px 20px;
}

.solution-group {
    display: flex;
    flex-direction: row;
}

.solution-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.MuiPaper-root {
    width: 40%;
}


body {
  width: 100%;
  background-color: #EEE2dc;
}
